<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Otoritas',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Otoritas',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
    },
	methods: {
      async Getdata() {
        try {
            let response = await axios.get(this.api+"/otoritas/index", Utils.authHeader());
            this.data = response.data;
        } catch (err) {
            console.error(err);
        }
      },
      async openEdit(id) {
          document.location = '/otoritasaction/edit/' + id
      },
      async openDetail(id) {
          document.location = '/otoritasaction/detail/' + id
      },
      async openRemove(id) {
		if (confirm('Anda Yakin akan menghapus Data')) {
		const item = this.data.find((el) => el.f_otoritas_id === id);
        const res =  await axios.delete(this.api+"/otoritas/delete/"+item.f_otoritas_id, Utils.authHeader());
		this.alert = true
        this.message = res.data.message
        this.Getdata()
		} else {
        
		}
	  }
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									<br>&nbsp;&nbsp;
									<b-button variant="dark" to="/otoritasaction/tambah">Tambah Data</b-button>
									<br>
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>
									<br>
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Hak Akses</th>
											<th scope="col">Nama Website</th>
											<th scope="col">Action</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_otoritas_id">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.f_otoritas}}</td>
											<td>{{tampil.fn_otoritas_website}}</td>
											<td>
											<b-button variant="info" @click="openDetail(tampil.f_otoritas_id)">Detail Data</b-button>
											&nbsp;
											<b-button variant="warning" @click="openEdit(tampil.f_otoritas_id)">Edit Data</b-button>
											&nbsp;
											<b-button variant="danger" @click="openRemove(tampil.f_otoritas_id)">Delete Data</b-button>
											</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
